<script>
import { ref} from "vue";
import Header from "../../components/Header.vue";


const setup = {

  url: process.env.VUE_APP_URL_API_COLLABORATORS

};

const columns = [
  { name: 'avatar', label: '', field: 'avatar', sortable: true, style: 'width: 40px' },
  {
    name: 'name',
    required: true,
    label: 'Nome',
    align: 'left',
    field: 'name',
    sortable: true
  },
  { name: 'edit', label: '', field: 'edit' }

]

const rows = [
  {
    avatar: '',
    name: '',
    edit: ''
  }
]


export default {
  name: "Home",
  search: "Pesquisar",
  components: {
    Header,
  },
  setup() {

    return {
      setup: setup,
      filter: ref(""),
      dense: ref(false),
      columns,
      rows: ref(rows)
      

      
    };
  },
  data() {

    return {
      loading: false,
      pageTitle: "Lista de Colaboradores",
      btnAddLabel: "",
      btnAddIcon: "las la-user-nurse",
      btnAddToolTiplabel: "Cadastrar Novo Colaborador",
      searchPlaceholder: "Pesquisar",
      collaborators: null,
      del: null,
      patch: null,
    };
  },
  methods: {
    async getApi() {
      const req = await fetch(this.setup.url);
      this.collaborators = await req.json();
      this.rows = this.collaborators.data;

    },
    async deleteApi(id) {
      const req = await fetch(this.setup.url +'/'+ id, {
        method: "DELETE",
      });

      this.del = await req.json();

      this.getApi();
    },
    async updatedApi(event, id) {
      const option = event.target;

      const dataJson = JSON.stringify({ status: option });

      const req = await fetch(this.setup.urlId + id, {
        method: "PATCH",
        headers: {
          "Content-Type": "application/json",
        },
        body: dataJson,
      });

      this.patch = await req.json();
      console.log(this.patch);
    },
  },
  mounted() {
    this.getApi();
  },
};
</script>
